import { number } from 'prop-types';
import React from 'react';

export const Sum = ({ a, b }) => (
  <p>
    Sum of
    {' '}
    {a}
    {' '}
    and
    {' '}
    {b}
    {' '}
    is
    {' '}
    {a + b}
  </p>
);

Sum.propTypes = {
  a: number,
  b: number,
};

Sum.defaultProps = {
  a: 0,
  b: 0,
};
